.header {
  display: flex;
  justify-content:space-around;
  align-items: center;
  background-color: skyblue;
  /* position: sticky; */
  margin: 0;
  width: 100vw;
  /* padding: 20vw; */
}

@media screen and (min-width: 1080px) {
  .header {
    height: 25vh;
  }
}
@media screen and (max-width: 1080px) {
  .header {
    height: 30vh;
  }
}
.title {
  color: white;
  font-size: 4em;
  font-weight: bold;

}
.divTitle {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}